@import url(https://fonts.googleapis.com/css2?family=Assistant:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
    padding: 0px;
    margin: 0px;
}

body {
    direction: rtl;
}


header {
    /* background-color: #3d4257; */
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-bottom: 16px;
}

.topTitle {
    height: 55px;
}

.topTitle>h3 {
    text-align: center;
    font-size: 18px;
    color: #B7BCCC;
    padding-top: 16px;
    font-family: 'Assistant', sans-serif;
}

.topTitle>span {
    position: absolute;
    top: 16px;
    right: 16px;
    font-size: 18px;
    color: #B7BCCC;
}
.contents {
  width: 100%;
  height: 50%;
}


.block {
    padding: 8px;
    width: 90%;
    margin: 0 auto;
    border-bottom: 2px solid #E2E2EA;
}

.block>.title {
    color: #324483;
    font-size: 16px;
    font-family: 'Assistant', sans-serif;
}

.block>.link {
    color: #717480;
    font-size: 12px;
    font-family: 'Assistant', sans-serif;
}

.block>span {
    align-content: left;
}
